:root {
  --naailblog-primary-color: #285CFF;
  --naailblog-secondary-color: #003BF5;
  --naailblog-heading-color: #182D70;
  --naailblog-text-color: #364574;
  --naailblog-boreder-color: #E0E4F1;
  --naailblog-base-ff: "sans-serif";
  --naailblog-base-fs: 16px;
  --naailblog-base-lh: 1.5;
  --naailblog-base-fw: 400;
  --naailblog-h-ff: "sans-serif";
  --naailblog-h-lh: 1.2;
  --naailblog-h-fw: 700;
  --naailblog-h1-fs: 50px;
  --naailblog-h2-fs: 40px;
  --naailblog-h3-fs: 36px;
  --naailblog-h4-fs: 28px;
  --naailblog-h5-fs: 20px;
  --naailblog-h6-fs: 18px;
}

body {
  font-size: var(--naailblog-base-fs);
  font-weight: var(--naailblog-base-fw);
  line-height: var(--naailblog-base-lh);
  color: var(--naailblog-text-color);
  background-color: #f9f9f9;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: var(--naailblog-h-fw);
  line-height: var(--naailblog-h-lh);
  color: var(--naailblog-heading-color);
}
h1 a:not(:hover), h2 a:not(:hover), h3 a:not(:hover), h4 a:not(:hover), h5 a:not(:hover), h6 a:not(:hover) {
  color: var(--naailblog-heading-color);
}

h1 {
  font-size: var(--naailblog-h1-fs);
}

h2 {
  font-size: var(--naailblog-h2-fs);
}

h3 {
  font-size: var(--naailblog-h3-fs);
}

h4 {
  font-size: var(--naailblog-h4-fs);
}

h5 {
  font-size: var(--naailblog-h5-fs);
}

h6 {
  font-size: var(--naailblog-h6-fs);
}

a {
  color: var(--naailblog-text-color);
  text-decoration: underline;
}
a:hover {
  color: var(--naailblog-secondary-color);
}

img {
  max-width: 100%;
  transition: 0.3s;
}

.fs-small {
  font-size: 14px;
}

.naailblog-btn {
  display: inline-block;
  border: none;
  outline: none;
  border-radius: 5px;
  padding: 15px 30px;
  text-decoration: none;
  color: #fff;
  background-color: var(--naailblog-primary-color);
}
.naailblog-btn:hover {
  color: #fff;
  background-color: var(--naailblog-secondary-color);
}

.naailblog-link-btn {
  display: inline-block;
  border: none;
  outline: none;
  font-weight: 500;
  color: var(--naailblog-primary-color);
}
.naailblog-link-btn:hover {
  color: var(--naailblog-secondary-color);
}

.master-header {
  border-bottom: 1px solid var(--naailblog-boreder-color);
}
.master-header .container {
  display: flex;
  gap: 30px;
}
.master-header .navigation {
  flex: 1;
}
.master-header .navigation ul {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  gap: 30px;
  margin: 0;
  padding: 0;
}
.master-header .menu-item a {
  display: block;
  padding: 10px;
  font-weight: 500;
  text-decoration: none;
}

.post {
  border: 1px solid var(--naailblog-boreder-color);
  background-color: #fff;
  box-shadow: 0px 2px 6px 0px rgba(0, 1, 1, 0.03);
  padding: 0px 20px;
  margin-bottom: 20px;
  overflow: hidden;
}
.post .thumbnaail {
  margin-left: -20px;
  margin-right: -20px;
  overflow: hidden;
}
.post .post-meta {
  margin: 20px 0;
}
.post .post-meta * {
  vertical-align: middle;
}
.post .calendar-icon {
  display: inline-block;
  margin-right: 10px;
  line-height: 1;
}
.post .calendar-icon svg {
  fill: var(--naailblog-primary-color);
}
.post .post-title {
  margin-bottom: 20px;
}
.post:hover .thumbnaail img {
  transform: scale(1.1);
}

.naailblog-pagination {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 30px;
  margin: 30px auto;
}
.naailblog-pagination .pagination-item {
  padding: 10px 15px;
  border: 1px solid var(--naailblog-boreder-color);
  cursor: pointer;
  transition: 0.3s;
}
.naailblog-pagination .pagination-item:hover, .naailblog-pagination .pagination-item.active {
  background-color: var(--naailblog-primary-color);
  color: #fff;
}